<template>
  <div class="container">
    <div class="messaging">
      <div class="inbox_msg">
        <div class="inbox_people">
          <div class="headind_srch">
            <div class="recent_heading">
              <h4>Recent</h4>
            </div>
            <div class="srch_bar">
              <div class="stylish-input-group">
                <input type="text" class="search-bar" placeholder="Search" />
                <span class="input-group-addon">
                  <button type="button">
                    <i class="fa fa-search" aria-hidden="true"></i>
                  </button>
                </span>
              </div>
            </div>
          </div>
          <div class="inbox_chat">
            <!-- contacts -->
            <div
                v-for="(contact, index) in contacts"
                :key="contact.id"
                @click="selectContact(index, contact)"
                :class="{'active_chat': index == selectedIndex}"
                class="chat_list"
            >
              <div class="chat_people">
                <div class="chat_img">
                  <v-avatar><img :src="`${storage}/${contact.avatar}`" alt="sunil" /></v-avatar>
                </div>
                <div class="chat_ib">
                  <h5>
                    {{ `${contact.prenom} ${contact.nom}` }}
                    <span class="chat_date">Dec 25</span>
                  </h5>
                  <p>
                    Test, which is a new approach to have all solutions
                    astrology under one roof.
                  </p>
                </div>
              </div>
            </div>
            <!-- end contacts -->
          </div>
        </div>
        <div class="mesgs">
          <div class="msg_history">
            <!-- MESSAGES -->
            <div v-if="selectedContact">

            

                <div
                    v-for="message in messages"
                    :key="message.id" 
                    :class="(message.to == selectedContact.id)?'outgoing_msg':'received_msg'"
                >
                    <div :class="(message.to == selectedContact.id)?'sent_msg':'received_withd_msg'">
                    <p>
                        {{ message.text }}
                    </p>
                    <span class="time_date">11:01 AM | June 9</span>
                    </div>
                </div>

            
            </div>

          </div>
          <div class="type_msg">
            <div class="input_msg_write">
              <!-- <input type="text" class="write_msg" placeholder="Type a message" /> -->
              <v-text-field @keydown.enter="send" placeholder="Message..." v-model="message" class="write_msg"></v-text-field>
              <button class="msg_send_btn" type="button">
                <v-icon
                    color="#fff"
                    small
                >send</v-icon>
              </button>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios';
// import { Echo, Pusher } from 'laravel-echo';
import { mapGetters } from 'vuex';

export default {
    data() {
        return {
            selectedContact: null,
            messages: [],
            contacts: [],
            storage: `${process.env.VUE_APP_API_LINK}storage`,
            message: '',
            selectedIndex: 0
        }
    },
    sockets:{
      connect() {
        console.log('connected');
      }
    },
    created () {
      // this.$socket.on('connection', ()=>{
      //   console.log('connected');
        
      // })
      // window.Pusher = Pusher;
      // let echo = new Echo({
      //   broadcaster: 'pusher',
      //   cluster: 'eu',
      //   key: 'aa932cc6d5e8b13d3634',
      //   wsHost: 'http://api.test/'
      // });

      //   echo.private(`messages.${this.user.id}"`)
      //       .listen('NewMessage', (e)=>{
      //           if (this.selectedContact && e.message.from == this.selectedContact.id) {
      //               this.messages.push(e.message);
      //           }else{
      //               alert(e.message.text)
      //           }
      //       })

        axios.get('/contacts')
                .then(res => {
                    this.contacts = res.data;
                });
    },
    computed: {
        ...mapGetters({
            user: 'auth/user'
        })
    },
    methods: {
        send() {
            if (this.message == '' || !this.selectedContact) {
                return;
            }
            
            axios.post('/conversation/send',{
                contact_id: this.selectedContact.id,
                text: this.message
            }).then(res=>{
                this.messages.push(res.data);
            })

            this.message = '';
        },
        selectContact(i, c){

            this.selectedIndex = i;

            axios.get(`/conversation/${c.id}`)
                    .then(res=>{
                        this.messages = res.data;
                        this.selectedContact = c;
                    })
        }
    },
};
</script>

<style lang="scss" scoped>
.container {
  max-width: 1170px;
  margin: auto;
}
img {
  max-width: 100%;
}
.inbox_people {
  background: #f8f8f8 none repeat scroll 0 0;
  float: left;
  overflow: hidden;
  width: 40%;
  border-right: 1px solid #c4c4c4;
}
.inbox_msg {
  border: 1px solid #c4c4c4;
  clear: both;
  overflow: hidden;
}
.top_spac {
  margin: 20px 0 0;
}

.recent_heading {
  float: left;
  width: 40%;
}
.srch_bar {
  display: inline-block;
  text-align: right;
  width: 60%;
}
.headind_srch {
  padding: 10px 29px 10px 20px;
  overflow: hidden;
  border-bottom: 1px solid #c4c4c4;
}

.recent_heading h4 {
  color: #7d3cff;
  font-size: 21px;
  margin: auto;
}
.srch_bar input {
  border: 1px solid #cdcdcd;
  border-width: 0 0 1px 0;
  width: 80%;
  padding: 2px 0 4px 6px;
  background: none;
}
.srch_bar .input-group-addon button {
  background: rgba(0, 0, 0, 0) none repeat scroll 0 0;
  border: medium none;
  padding: 0;
  color: #707070;
  font-size: 18px;
}
.srch_bar .input-group-addon {
  margin: 0 0 0 -27px;
}

.chat_ib h5 {
  font-size: 15px;
  color: #464646;
  margin: 0 0 8px 0;
}
.chat_ib h5 span {
  font-size: 13px;
  float: right;
}
.chat_ib p {
  font-size: 14px;
  color: #989898;
  margin: auto;
}
.chat_img {
  float: left;
  width: 11%;
}
.chat_ib {
  float: left;
  padding: 0 0 0 15px;
  width: 88%;
}

.chat_people {
  overflow: hidden;
  clear: both;
}
.chat_list {
  border-bottom: 1px solid #c4c4c4;
  margin: 0;
  padding: 18px 16px 10px;
  cursor: pointer;
}
.inbox_chat {
  height: 550px;
  overflow-y: scroll;
}

.active_chat {
  background: #ebebeb;
}

.incoming_msg_img {
  display: inline-block;
  width: 6%;
}
.received_msg {
  display: inline-block;
  padding: 0 0 0 10px;
  vertical-align: top;
  width: 92%;
}
.received_withd_msg p {
  background: #ebebeb none repeat scroll 0 0;
  border-radius: 3px;
  color: #646464;
  font-size: 14px;
  margin: 0;
  padding: 5px 10px 5px 12px;
  width: 100%;
}
.time_date {
  color: #747474;
  display: block;
  font-size: 12px;
  margin: 8px 0 0;
}
.received_withd_msg {
  width: 57%;
}
.mesgs {
  float: left;
  padding: 30px 15px 0 25px;
  width: 60%;
}

.sent_msg p {
  background: #7d3cff none repeat scroll 0 0;
  border-radius: 3px;
  font-size: 14px;
  margin: 0;
  color: #fff;
  padding: 5px 10px 5px 12px;
  width: 100%;
}
.outgoing_msg {
  overflow: hidden;
  margin: 26px 0 26px;
}
.sent_msg {
  float: right;
  width: 46%;
}
.input_msg_write input {
  background: rgba(0, 0, 0, 0) none repeat scroll 0 0;
  border: medium none;
  color: #4c4c4c;
  font-size: 15px;
  min-height: 48px;
  width: 100%;
}

.type_msg {
  border-top: 1px solid #c4c4c4;
  position: relative;
}
.msg_send_btn {
  background: #7d3cff none repeat scroll 0 0;
  border: medium none;
  border-radius: 50%;
  color: #fff;
  cursor: pointer;
  font-size: 17px;
  height: 33px;
  position: absolute;
  right: 0;
  top: 11px;
  width: 33px;
}
.messaging {
  padding: 0 0 50px 0;
}
.msg_history {
  height: 516px;
  overflow-y: auto;
}
</style>